body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    background: #eee;
}

.header-color {
    background: #191919 !important;  
    color: #FFF !important;
    box-shadow: none !important;
    box-shadow: initial !important;
}

.iconbuttonz{
  color: #FFF !important;
  float: right;
}

.mdl-layout-title {
    margin-left: -50px !important;
    color: #FFF;
    font-family: 'Righteous', cursive !important;
    border: 2px solid #fff;
    font-weight: bold;
    padding: 5px;
}


@media only screen and (max-width: 840px) {
    .header-color {
        box-shadow: none!important;
    }
}


.landingcontainer {
    width: 100%;
    margin: auto;
}

.landing-grid {
    text-align: center;
    background: #191919;
    height: 350px;
}

.landing-grid2 {
    margin-top: 100px !important;
    text-align: center;
}

.landing-grid3 {
    margin-top: 100px !important;
    text-align: center;
}

.avatar-img {
    height: 450px;
    display: block;
    margin: auto;
    background-attachment: fixed;
}



#left-cell {
    text-align: center;
    overflow: hidden;
}



#right-cell {
    text-align: center;
    overflow: hidden;
}

.cellText {
    font-family: 'Special Elite', sans-serif;
    padding: 20px;
    margin-top: 10px;
    width: 85%;
    margin: 0 auto;
    text-align:left;
    font-size: 1.1em;
    letter-spacing: 1px;
}

.cellText2 {
    font-family: 'Special Elite', cursive;
    margin: auto;
    font-size: 3em;
    font-weight: bold;
}

.cellText3 {
    font-family: 'Special Elite', cursive;
    margin: auto;
    font-size: 1.1em;
    font-weight: bold;
    width: 70%;
}

.html {
    color: #177e89;
}

.bootstrap {
    color: #084c61;
}

.js {
    color: #801040;
}

.wordpress {
    color: #ffc857;
}

.react {
    color: #005900;
}


.banner-text {
    color: #fff;
    width: 60%;
    margin: auto;
    margin-top: 70px;
    border: 3px dotted #fff;
    padding: 20px;
    font-family: 'Special Elite', cursive;
}

.banner-code {
    text-align: center;
    margin: 0;
    color: #fff;
    display: block;
    font-family: 'Amatic SC', cursive;
    font-weight: bold;
    font-size:2em;
    margin-top: 2rem;
}

.frontend {
    color:#c70039;
}

.banner-text h1 {
    color: #fff;
    font-weight: bold;
    font-size: 3em;
}

.banner-text h2 {
    color: #fff;
    font-weight: bold;
    font-size: 2em;
}

.banner-text hr {
    border-top: 5px dotted white;
    width: 50%;
    margin: auto;
}

.banner-text p {
    color: white;
    font-size: 1.5em;
    padding: 1em;
}

.social-links {
    display: inline;
    text-align: center;
    width: 50%;
    margin: auto;
}

.social-links i {
    color: #191919;
    font-size: 1.5em;
    padding: 8px;
}

@media only screen and (max-width: 840px) {
    #right-cell {
        width: 100%;
    }

    .cellText2 {
        margin-top: 20px;
    }

    .cellText, .cellText3 {
        width: 90%;
    }

    .banner-code {
        font-size: 1.5em;
    }

    .backimg {
        background-position: 35% 90%;
    }

    #left-cell {
        width: 100%;
    }

    .mdl-grid {
        padding: 0 !important;
        padding: initial !important;
    }

    .banner-text {
        margin-top: 40px;
        font-size: 2em;
        font-weight: bold;
    }

    .landing-grid2 {
        margin-top: 50px !important;
        text-align: center;
    }

}

.right-about {
    margin: auto !important;
    text-align: left;
}

.left-about {
    margin: 50px auto 0 !important;
}

.about-head {
    font-family: 'Special Elite', sans-serif;
    font-size: 3em;
    font-weight: bold;
    margin: 20px auto;
    padding-top: 20px;
}

.about-head2 {
    font-family: 'Special Elite', sans-serif;
    color: grey;
    margin: auto;
    font-size: 2em;
    font-weight: bold;
    margin: 20px auto;
}

.about-text {
    font-family: 'Special Elite', sans-serif;
    padding: 20px;
    margin-top: 10px;
    width: 70%;
    margin: 0 auto;
    text-align:left;
    font-size: 1.1em;
    letter-spacing: 1px;
}

.education-year {
    font-family: 'Special Elite', sans-serif;
    font-size: 1em;
    font-weight: bold;
}

.education-name {
    font-family: 'Special Elite', sans-serif;
    font-size: 2em;
    font-weight: bold;
    margin: 20px auto 5px;
    color: #c70039;
}

.education-desc {
    font-family: 'Special Elite', sans-serif;
    margin-top: 10px;
    margin: 0 auto;
    text-align:left;
    font-size: 1.1em;
    letter-spacing: 1px;
}

.education-container {
    border: 3px dotted #D0D0D0;
    width: 80%;
}

.additionalClassForHead
{
    background-color: #eee !important;
    color: #c70039 !important;
    font-family: 'Special Elite', sans-serif;
    font-weight: bold;
    font-size: 1em;
}

.panel-accordion {
    background-color: #eee !important;
    width: 100%;
    padding: 0;
    margin: 0;
    margin-left: -20px;
}

.skills-container {
    font-family: 'Special Elite', sans-serif;
    margin: 0 auto;
    width: 70%;
    text-align: center;
}


@media only screen and (max-width: 840px) {
   .education-container {
       width: 100%;
       margin: 5px auto;
   }

   .about-text {
       width: 90%;
   }

    .contact-grid {
        width: 100%;
    }

    .mdl-list__item{
        padding:0 !important;
        margin-left: -5px;
    }

    .education-name {
        font-size: 1.7em;
    }

    .about-head {
        font-size: 2.5em;
        padding: 5px;
    }

    
}



.contact-container {
    margin: auto;
    width: 100%;
    min-height: 100%;
    justify-content: center;
    padding-top: 2em;
    overflow: hidden;
}

.contact-grid {
    text-align: center;
    justify-content: center;
    width: 50%;
    color: #191919;
    margin-bottom: 20px;
}

.contact-grid h2 {
    font-family: 'Special Elite', sans-serif;
}

.contact-grid p {
    font-family: 'Special Elite', sans-serif;
    text-align: left;
}


.contact-list i {
    font-size: 20px;
    padding-right: 1rem;
}

.contact-list {
    height: 500px;
}

.contact-list a {
    text-decoration: none;
    color: #191919;
}

.mdl-list__item-primary-content {
    margin-left: 30px;
}

.contact-text {
    font-family: 'Special Elite', sans-serif;
    padding: 20px;
    margin-top: 10px;
    width: 90%;
    padding: 20px;
    margin: 0 auto;
    text-align:left;
    font-size: 1.1em;
    letter-spacing: 1px;
}




@media only screen and (max-width: 840px) {
    #contact-left {
        border-right: none;
        border-bottom: 2px dotted #c70039;
    }

    .contact-grid {
        width: 100%;
    }

    .mdl-list__item{
        padding:0 !important;
        margin-left: -5px;
    }

    
}
.projects-grid {
    display: flex;
    flex-wrap: wrap;
}

.card-container {
 margin-top: 40px !important;
 margin-bottom: 5px !important;
}

.grid-container {
    width: 100%;
    text-align: center;
}

.project-linebreak {
    width: 30px;
    border-top: 5px solid #c70039;
    margin: auto;
    margin: 20px auto;
}

.project-text {
    font-family: 'Special Elite', sans-serif;
    padding: 20px;
    margin-top: 10px;
    width: 70%;
    margin: 0 auto;
    text-align:left;
    font-size: 1.1em;
    letter-spacing: 1px;
}

.project-head {
    font-family: 'Special Elite', sans-serif;
    margin: auto;
    font-size: 3em;
    font-weight: bold;
    margin: 20px auto;
}

.frontend {
    color:#c70039;
}

.mdl-tabs__tab {
    font-family: 'Special Elite', sans-serif;
    color: #191919;
    font-weight: bold !important;
}

.mdl-card__supporting-text {
    color:#323232 !important;
    font-weight: bold !important;
    font-family: 'Raleway', cursive, sans-serif;
    letter-spacing: 2px;
    overflow: hidden;
    text-align: left !important;
    min-height: 60px;
}



.mdl-card__title-text {
    color: #FFF !important;
    font-weight: bold !important;
    font-family: 'Special Elite', cursive, sans-serif;
}

.mdl-button {
    font-weight: bold !important;
    font-family: 'Special Elite', cursive, sans-serif
}

@media only screen and (max-width: 840px) {
    .project-head {
        font-size: 2em;
        padding: 20px;
    }

    .project-text {
        width: 90%;
    }

    
}


